body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Paris2024", sans-serif;
}

.custom-navbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-color: transparent;
  padding: 1rem 2rem;
}

.navbar-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.left-section,
.center-section,
.right-section {
  display: flex;
  align-items: center;
}

.left-section,
.right-section {
  flex: 1;
}

.center-section {
  flex: 1;
  justify-content: center;
}

.hamburger-menu {
  margin-right: 1rem;
}

.nav-item {
  margin: 0 1rem;
  text-decoration: none;
  color: #fff;
}

.search-bar {
  padding: 0.5rem;
  border-radius: 20px;
  border: 1px solid #fff;
  background-color: transparent;
  color: #fff;
}

.search-bar::placeholder {
  color: #fff;
  opacity: 1;
}

.countdown-banner {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 8rem;
}
.content-section {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 40px 20px;
  color: white;
}

.content-box {
  flex: 1;
  padding: 20px;
}
.content-text {
  line-height: 96px;
}

.left-box {
  max-width: 60%;
}

.right-box {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.content-box h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.content-box p {
  margin-bottom: 20px;
  line-height: 1.3;
}

.coral-button {
  background-color: #d7c378;
  color: white;
  border: none;
  padding: 10px 20px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border-radius: 8px;
}
.white-button {
  background-color: #fff;
  color: #212121;
  border: none;
  padding: 10px 20px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border-radius: 40px;
}

.coral-button:hover {
  background-color: #d7c380;
}

.registration-box,
.accreditation-box {
  padding: 20px;
  border-radius: 10px;
}

@media (max-width: 768px) {
  .content-section {
    flex-direction: column;
  }

  .left-box,
  .right-box {
    max-width: 100%;
  }
}

.latest-news-section {
  background-color: #f5f5f5;
  padding: 40px 20px;
}

.latest-news-section h2 {
  color: #800080;
  font-size: 2rem;
  margin-bottom: 20px;
}

.news-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.news-item {
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.news-item img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.news-content {
  padding: 15px;
}

.news-content h3 {
  font-size: 1.2rem;
  margin-bottom: 10px;
}

.news-content p {
  font-size: 0.9rem;
  color: #666;
}

@media (max-width: 1024px) {
  .news-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .news-grid {
    grid-template-columns: 1fr;
  }
}

.medal-tally-section {
  display: flex;
  justify-content: space-between;
  padding: 40px 20px;
  background-color: #f5f5f5;
}

.medal-tally-container {
  flex: 1;
  max-width: 60%;
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.medal-tally-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.medal-icon {
  font-size: 2rem;
  margin-right: 10px;
}

.medal-tally-header h2 {
  font-size: 2rem;
  color: #4a4a4a;
}

.medal-table {
  width: 100%;
  border-collapse: collapse;
}

.medal-table th,
.medal-table td {
  padding: 10px;
  text-align: center;
  border-bottom: 1px solid #e0e0e0;
}

.medal-table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

.medal-tally-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.refresh-button,
.view-more-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.refresh-button {
  background-color: #800080;
  color: white;
}

.view-more-button {
  background-color: #f0f0f0;
  color: #4a4a4a;
}

.athlete-image {
  flex: 1;
  max-width: 35%;
  border-radius: 10px;
  overflow: hidden;
}

.athlete-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 768px) {
  .medal-tally-section {
    flex-direction: column;
  }

  .medal-tally-container,
  .athlete-image {
    max-width: 100%;
  }

  .athlete-image {
    margin-top: 20px;
    height: 300px;
  }
}

.schedule-section {
  background-color: #f5f5f5;
  padding: 40px 20px;
  border-radius: 20px;
}

.schedule-section h2 {
  color: #8e44ad;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.schedule-filters {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
}

.date-select,
.game-select {
  padding: 10px;
  margin-left: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.schedule-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 10px;
}

.schedule-table th {
  background-color: #8e44ad;
  color: white;
  padding: 15px;
  text-align: left;
}

.schedule-table td {
  background-color: white;
  padding: 15px;
}

.schedule-table tr td:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.schedule-table tr td:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.sport-icon {
  width: 30px;
  height: 30px;
  background-color: #8e44ad;
  border-radius: 50%;
  display: inline-block;
  margin-right: 10px;
  vertical-align: middle;
}

@media (max-width: 768px) {
  .schedule-filters {
    flex-direction: column;
  }

  .date-select,
  .game-select {
    margin-left: 0;
    margin-bottom: 10px;
  }

  .schedule-table {
    font-size: 0.8rem;
  }
}

.mascot-container {
  display: flex;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.mascot-content {
  flex: 1;
  padding: 40px;
}

h3 {
  color: #8b4b8b;
  font-size: 18px;
  margin-bottom: 0;
}

h1 {
  color: #8b4b8b;
  font-size: 48px;
  font-weight: bold;
  margin-top: 10px;
  line-height: 1;
}

p {
  color: #666;
  font-size: 14px;
  margin: 20px 0;
}

.meet-mascot-btn {
  background-color: #8b4b8b;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 25px;
  font-weight: bold;
  cursor: pointer;
}

.mascot-image {
  flex: 1;
  background: linear-gradient(135deg, #9370db, #483d8b);
  position: relative;
  overflow: hidden;
}

.mascot-image::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 90%;
  background-color: white;
  border-radius: 100px 100px 0 0;
}

@media (max-width: 768px) {
  .mascot-container {
    flex-direction: column;
  }

  .mascot-image {
    height: 200px;
  }
}

.conversation-container {
  display: flex;
  background-color: #ffffff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.stats-section {
  flex: 1;
  padding: 40px;
}

.stats-section h2 {
  color: #333;
  font-size: 24px;
  margin-bottom: 0;
}

.stats-section h1 {
  color: #8b4b8b;
  font-size: 48px;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
}

.stats-section p {
  color: #8b4b8b;
  font-size: 18px;
  margin-bottom: 30px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.stat-item {
  background-color: #f8f8f8;
  padding: 15px;
  border-radius: 8px;
}

.stat-item h3 {
  color: #8b4b8b;
  font-size: 14px;
  margin-bottom: 10px;
}

.stat-value {
  font-size: 36px;
  font-weight: bold;
  color: #333;
}

.stat-label {
  font-size: 12px;
  color: #666;
}

.image-section {
  flex: 1;
  background-color: #f0f0f0;
}

.image-section img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 768px) {
  .conversation-container {
    flex-direction: column;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }

  .image-section {
    height: 300px;
  }
}

.sports-grid-container {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.sports-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.sports-header h2 {
  color: #8b4b8b;
  font-size: 32px;
  font-weight: bold;
}

.view-more-btn {
  background-color: transparent;
  color: #8b4b8b;
  border: none;
  font-size: 14px;
  cursor: pointer;
}

.sports-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 15px;
}

.sport-card {
  background: linear-gradient(135deg, #8b4b8b, #483d8b);
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  aspect-ratio: 1 / 1;
  position: relative;
}

.sport-card img {
  width: 60%;
  height: 60%;
  object-fit: contain;
}

.sport-card p {
  color: white;
  font-size: 12px;
  margin-top: 10px;
  text-align: center;
}

@media (max-width: 1024px) {
  .sports-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 768px) {
  .sports-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 480px) {
  .sports-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
.follow-us-container {
  display: flex;
  background-color: #ffffff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.follow-us-content {
  flex: 1;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.follow-us-content h2 {
  color: #8b4b8b;
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
}

.social-icons {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
}

.social-icons svg {
  font-size: 24px;
  color: #8b4b8b;
}

.hashtag {
  font-size: 18px;
  color: #333;
}

.follow-us-image {
  flex: 1;
  overflow: hidden;
}

.follow-us-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 768px) {
  .follow-us-container {
    flex-direction: column;
  }

  .follow-us-image {
    height: 300px;
  }
}
